import React from "react";
import { useNeonRadioStyles } from "@mui-treasury/styles/radio/neon";
import { FormControlLabel, Typography, Radio, RadioGroup } from "@mui/material";
import { bannerCheckboxStylesHook } from "@mui-treasury/styles/checkbox/banner";

export default function CustomRadio(props) {
  const neonStyles = useNeonRadioStyles();
  const subtitleStyles = bannerCheckboxStylesHook.useSubtitle();
  return (
    <div>
      <Typography component="span" classes={subtitleStyles} color="secondary">
        {props.title}
      </Typography>
      <RadioGroup defaultValue={props.options[0]}>
        {props.options.map((option) => (
          <FormControlLabel
            key={option}
            control={
              <Radio
                value={option}
                disableRipple
                classes={neonStyles}
                checkedIcon={<span />}
                icon={<span />}
              />
            }
            label={
              <>
                <Typography
                  component="span"
                  classes={subtitleStyles}
                  color="secondary.dark"
                >
                  {option}
                </Typography>
              </>
            }
          />
        ))}
      </RadioGroup>
    </div>
  );
}
